
@media screen and (min-width : 320px) {
   .header {
    width: 80%;
    margin: 20px auto; 
    }
    .header__logos {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center; 
    }
    .header__logos a {
     height: fit-content;
    } 
    .header__dropmenu {
        margin-top: 46.5px;
        height: 600px;

        button {
            width: 170px;
            height: 52px;
            color: #1894FF;
            background-color: #E6F3FF;
            border: none;
            font-weight: bold;
            font-size: 14px;
            border-radius: 10px;
        }
        hr {
            width: 100%;
            margin-top: 27px;
            opacity: .1;
        }
        .dropmenu__items {
            margin-top: 32.5px;
        }
        h3 {
            font-size: 16px;
            color: #1894FF;
        }
        h3::before {
            content: '';
            border-radius: 25px;
            height: 24px;
            border: 2px solid #1894FF;
            margin-left: 14px;
        }
        li {
            list-style: none;
            margin-top: 33px;
            border-bottom: none;
            a {
                text-decoration: none;
                color: black;
                font-weight: lighter;
                padding-bottom: 10px;
            }
        :hover {
            color: #1894FF;
            border-bottom: 2px solid #1894FF;
        }
        }
    
    }
}


